.specs-list {
  column-count: 1;
  list-style-type: none;
  padding: 30px 15px;
  @include tablet {
    column-count: 2;
  }

  @include desktop {
    column-gap: 100px;
  }

  @include desktop-l {
    column-gap: 144px;
    padding: 80px 65px;
  }
}

.specs-list__item {
  break-inside: avoid-column;
}

.specs-list li h3 {
  color: $color-primary;
  font-weight: 500;

  @include desktop {
    margin-bottom: 13px;
  }
}


.specs-list p {
  line-height: 1.9;
  @include desktop {
    font-size: 20px;
    letter-spacing: -0.7px;
    margin: 0;
}
  }

.specs-list li {
  position: relative;

  @include desktop {
    margin-bottom: 57px;
  }
  
}

.specs-list li::before {
  content: ' ';
  width: 15px;
  height: 15px;
  border-radius: 50%;
  position: absolute;
  background-color: $color-primary;
  top: 5px;
  left: -20px;


  @include desktop-l {
    width: 40px;
    height: 40px;
    top: 2px;
    left: -91px;
  }
}