.page-header {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0, 50%;

  @include desktop {
    min-height: 576px;
    background-position: 50%, 50%;
  }
}

.page-header__inner {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  padding-right: 40px;
  padding-left: 40px;
  width: 100%;
  max-width: 1486px;
  padding-top: 60px;
  padding-bottom: 60px;

  @include laptop {
    padding-right: 70px;
    padding-left: 70px;
  }

  @include desktop {
    padding: 70px;
  }

  @include desktop-l {
    margin-right: auto;
    margin-left: auto;
  }
}

.page-header__text {
  padding-top: 48px;
  text-align: center;

  @include laptop {
    max-width: 480px;
    text-align: left;
  }

  @include desktop {
    width: 70%;
    max-width: 100%;
  }

  h1 {
    margin-bottom: 32px;
  }

  p {
    font-family: $font-secondary;
    font-weight: 300;
    line-height: 1.9;
    margin: 0;

    @include laptop {
      font-size: 20px;
    }
  }
}

.page-header__aside {
  width: 100%;

  @include desktop {
    width: calc(30% - 50px);
  }

  > * {
    margin-left: 0;
    margin-right: auto;

    @include desktop {
      margin-left: auto;
      margin-right: 0;
    }
  }
}

.page-header__aside--contact {
  @include laptop {
    position: absolute;
    right: 36px;
  }

  @include desktop {
    right: 72px;
  }

  > * {
    max-width: 364px;

    @include laptop {
      margin-left: auto;
      margin-right: 0;
    }
  }
}

.page-header__news-photo {
  position: relative;
}

.page-header__news-photo img {
  object-fit: cover;
  height: 326px;
  width: 100%;
}

.page-header__news-date {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 20px 54px;
  font-size: 20px;
}

.page-header__text h1 {
  font-size: 28px;
  font-weight: 500;
  @include desktop {
    font-size: 48px;
  }
}

.page-header__text p {
  font-size: 14px;
  font-weight: 300;
  line-height: 1.9;

  @include laptop {
    font-size: 20px;
  }
}
