.card {
  max-width: 405px;
  padding: 25px;
  box-shadow: 0 4px 45px 0 #00000014;
  background-color: #fff;

  @include desktop {
    padding: 50px;
  }

  h3 {
    font-weight: 500;
    font-family: $font-secondary;
    font-size: 28px;
    margin-bottom: 40px;
  }

  p {
    font-size: 15px;

    @include laptop {
      font-size: 20px;
    }
  }
}

.card--primary {
  background-color: $color-primary;
  color: #fff;
}

.card__body {
  padding: 24px 40px;
}

.card__footer {
  padding-top: 42px;

  @include tablet {
    padding: 34px 50px;
  }
}

.card-aside {
  max-width: 480px;
  padding: 52px;
}

.card-contact {
  max-width: 364px;
  padding: 70px;
}

.card.card--service {
  padding: 0;

  h3 {
    font-family: $font-primary;
    margin-bottom: 0;
    color: $color-primary;
  }

  p {
    font-family: $font-secondary;
  }
}
