.section-benefits {
  position: relative;
  overflow: hidden;
  height: 680px;
  background-image: url("../images/windmills.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 85% 0;
  padding: 50px 20px 40px;

  h2 {
    margin-bottom: 50px;
  }

  @include tablet {
    height: 1000px;
    background-size: cover;
    background-position: 0 0;
  }

  [data-tab-content] {
    background-repeat: no-repeat;
    background-size: contain;

    @include tablet {
      display: block;
    }
  }

  .sun {
    position: absolute;
    top: -140px;
    left: -24px;
    width: 883px;
    height: 720px;
    background-image: url("../images/sun.png");
  }

  .cloud1 {
    position: absolute;
    top: 280px;
    width: 416px;
    left: 0;
    height: 233px;
    background-image: url("../images/cloud1.png");
  }

  .cloud2 {
    position: absolute;
    top: 0;
    width: 752px;
    left: 19%;
    height: 425px;
    background-image: url("../images/cloud2.png");
  }

  .cloud3 {
    position: absolute;
    top: 0;
    left: 59%;
    width: 568px;
    height: 321px;
    background-image: url("../images/cloud3.png");
  }
}

.section-benefits__cards {
  display: flex;
  gap: 60px;
  position: relative;

  @include tablet {
    gap: 20px;
  }

  @include laptop {
    gap: 60px;
  }

  > * {
    width: 100%;
  }
}

.section-benefits__inner {
  position: relative;
  max-width: 1630px;
  margin-right: auto;
  margin-left: auto;
}

.section-benefits__links {
  margin-top: 65px;
  text-align: center;
  font-weight: 600;
  font-family: $font-secondary;
  font-size: 20px;
  color: $color-primary;

  a {
    font-size: 15px;
    display: block;
    max-width: 234px;
    margin: 0 auto;
    padding: 16px;
    background-color: $color-primary;
    color: #fff;
    font-weight: 400;
    text-decoration: none;

    @include desktop {
      max-width: 100%;
      font-weight: 600;
      font-size: 20px;
      background-color: transparent;
      text-decoration: underline;
      color: $color-primary;
    }
  }
}

.cards-tabs {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  width: 100%;
  border: 1px solid $color-primary;
  border-top: 0;
  padding: 20px;
  background-color: #fff;
  margin-top: -1px;
  position: relative;

  @include tablet {
    display: none;
  }
}

.cards-tabs__tab-button {
  padding: 0 0 10px;
  border-bottom: 3px solid #dadada;

  &.is-active {
    border-color: $color-primary;

    svg {
      fill: $color-primary;
    }
  }

  svg {
    fill: #dadada;
    height: 32px;
    pointer-events: none;
  }
}
