.news-list {
  display: grid;
  grid-template-columns: 1fr;
  list-style-type: none;
  gap: 40px;

  @include tablet {
    grid-template-columns: 1fr 1fr;
  }
  @include desktop {
    grid-template-columns: 1fr 1fr;
    gap: 72px;
    max-width: 928px;
    list-style-type: none;
  }
  
}


.news-list__item {
  box-shadow: 1px 1px 8px gray;
}

.page-news .pagination {
  justify-content: space-between;
  padding: 40px 0;
 

  @include desktop {
    padding: 82px 0;

  }
}