.section-contact-us {
  padding-bottom: 0 !important;
}

.section-contact-us .background-primary {

  padding: 30px 37px 0;
display: flex;
flex-direction: column-reverse;
position: relative;
height: 400px;

@include laptop {
  padding: 30px 37px 0;
  height: 412px;
}
}

.section-contact-us .background-primary img {
  margin-left: -44%;
  width: auto;
  height: 315px;
  z-index: 1;
  object-fit: cover;
  object-position: top;
  @media (min-width:450px) {
    height: 350px;
    margin-left: -38%;
  }
  
  @media (min-width:650px) {
    margin-left: -28%;
  }
  
  @include tablet { 
    margin-left: -26%;
    object-fit: contain;
    object-position: bottom;
  }

  @include laptop {
    height: 438px;
    margin-left: -31%;
  }
}

.section-contact-us__actions a {
  @include desktop {
    font-size: 20px;
  }
}

.section-contact-us__text .button {
  min-height: 54px;
  padding: 17px;
  width: 242px;
  margin-bottom: 30px;
  

  @include laptop {
    
    margin-bottom: 0px;
    margin-right: 30px;
  }



  @include desktop  {
    margin-right: 50px;
    padding: 25px;
    min-height: 72px;
  }
}

.section-contact-us__text h2 {
  font-size: 16px;
  margin-bottom: 20px;
  text-align: center;


  @media (min-width:450px) {
    text-align: right;
    font-size: 16px;
  }
   
  @media (min-width:650px)  {
    font-size: 20px;
  }
  
  @include laptop {
    text-align: left;
    font-size: 22px;
    
  }

  @include desktop {
    font-size: 38px;
  }

  @include desktop-l {
    font-size: 44px;
    margin-bottom: 44px;
  }
}

.section-contact-us__actions {
  display: flex;
  flex-direction: column;
  align-items: center;


  @media (min-width:450px) {
    align-items: end;
  }
  @include laptop {
    flex-direction: row;
    align-items: center;
  }

  @include desktop {
    
  }
}

.section-contact-us__text {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 12%;

  @media (min-width:450px) {
    top: 22%;
    right: 10%;
    width: 296px;
  }

  @media (min-width:650px) {
   right: 16%;
   width: 400px;
  }

  @include laptop {
   
    width: 45%;
    right: 10%;
  }

  @include desktop {
    width: 53%;
  }

}