.slider-news__controls {
  position: absolute;
  bottom: -72px;
  display: flex;
  justify-content: space-around;
  gap: 20px;
  width: 100%;

  @include tablet {
    bottom: auto;
    top: -100px;
    right: -15px;
    justify-content: flex-end;
    width: auto;
  }

  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    width: 52px;
    height: 52px;
    margin: 0;
  }
}
